<template>
  <div class="List">
    <top-menu @go="getLists"/>
    <div class="contents">
      <div class="contents-list">
        <ion-grid class="contents-list-grid" padding="9.7">
            <ion-row>
                <ion-col col-3 v-for="(data, index) in posts" :key="index">
                  <div class="list-item-wrapper" @click="goView(data.SEQ)" v-if="data.PHOTO.length > 0">
                    <img :src="'http://211.53.209.242:8080/static/' + getDefaultImage(data.PHOTO)" class="list-item-thumb col3" />
                    <p class="list-item-price">{{data.PRODUCT_PRICE}}원</p>
                    <p class="list-item-title">{{data.PRODUCT_NAME}}</p>
                    <p class="list-item-author"></p>
                  </div>
                </ion-col>
            </ion-row>
        </ion-grid>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '@/components/Header'
import API from '@/api.js'

export default {
  name: 'List',
  components: {
    'top-menu': Header
  },
  data: function () {
    return {
      posts: [],
      viewType: 'col-6',
      searchText: null,
      viewPopOver: false,
      viewOption: '전체보기'
    }
  },
  created() {
    this.getLists('all');
    this.EventBus.$on('DO_SEARCH_LIST', this.doSearch);
  },
  methods: {
    doSearch(payload){
      if(payload && payload != "") this.searchText = payload;
      else this.searchText = null;
      this.getLists('all');
    },
    getViewType(type){
      return (type == this.viewType) ? true : false;
    },
    getPopover(){
      return this.viewPopOver = !this.viewPopOver;
    },
    goView($id) {
      this.$router.push({ name: 'View', params: { id: $id }})
    },
    getLists (id) {
      let params = {};
      if(this.searchText && this.searchText != ""){
        params = {search: this.searchText};
      }
      let url = '/list/' + id;
      API.find(params, url)
        .then(res => {
          this.posts = res.data.data
        }).catch(() => {
          alert("서버와의 연결에 실패하였습니다.");
          //console.error(JSON.parse(e));
        })
    },
    changeView (type){
      this.getPopover();
      switch(type){
        case "ALL":
          this.viewOption = "전체보기";
          this.getLists('all');
          break;
        case "SALE":
          this.viewOption = "판매 중인 상품";
          this.getLists('sale');
          break;
        case "SOLD":
          this.viewOption = "판매 완료된 상품";
          this.getLists('sold');
          break;
        case "LAST":
          this.viewOption = "최신순";
          this.getLists('last');
          break;
      }
    },
    setType (i) {
      this.viewType = `col-${i}`;
    },
    goWrite () {
      this.$router.push({name: 'Write'})
    },
    getDefaultImage(photo){
      let photos = JSON.parse(photo);
      return photos[0];
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/styles/global.scss";
@import "@/styles/pages/list.scss";
</style>
