<template>
  <section class="header">
    <div class="top">
      <div class="logo" @click="goList()"></div>
      <div class="menu">
        <ul class="menu-list">
          <li class="list-item" @click="goWrite()">판매 등록</li>
          <li class="list-item" @click="goList()">판매 목록</li>
          <li class="list-item" @click="goLogout()">로그아웃</li>
        </ul>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      searchText: ""
    }
  },
  methods: {
    goMyPage () {
      let User = this.$store.getters.getUser || null;
      if(User.accessToken != undefined) this.$router.push({name: 'MyPage'})
      else {
        alert("로그인이 필요합니다.");
        this.$router.push({name: 'Login'});
      }
    },
    goLogout(){
      this.$store.dispatch('LOGOUT');
      this.$router.push({name: 'Login'});
    },
    doSearch(){
      this.EventBus.$emit('DO_SEARCH_LIST', this.searchText);
      return false;
    },
    goWrite () {
      this.$router.push({name: 'Write'})
    },
    goList () {
      this.$router.push({name: 'List'})
    },
  }
}
</script>
<style lang="scss" scoped>
@import "@/styles/global.scss";
@import "@/styles/components/header.scss";
</style>
