<template>
  <div class="View">
    <top-menu/>
    <div class="contents">
      <section class="picture-box">
        <carousel :center-mode="true"  :perPageCustom="[[0, 1]]">
          <slide class="picture-slide" v-for="(photo, index) in posts.PHOTO" :key="`thumb-${index}`">
            <img :src="getImageURI(photo)" class="picture-image" />
          </slide>
        </carousel>
      </section>
      <section class="article">
        <div class="status-bar">
          <span class="status-icon" :class="{active : isActive}">판매중</span>
          <span class="status-icon" :class="{active : !isStatus}" v-if="!isStatus">중고</span>
          <span class="status-icon" :class="{active : isStatus}" v-if="isStatus">새제품</span>
          <span class="status-icon" :class="{active : isDelivery}" v-if="isDelivery">직거래</span>
          <span class="status-icon" :class="{active : !isDelivery}" v-if="!isDelivery">택배</span>
          <span class="status-icon" :class="{active : !isFee}" v-if="!isFee">무료배송</span>
          <span class="status-icon" :class="{active : isFee}" v-if="isFee">배송별도</span>
          <span class="status-navigation">용품 > 골프백</span>
        </div>
        <p class="article-title">
          {{posts.PRODUCT_NAME}}
        </p>
        <div class="article-info">
          <span class="info-price">{{posts.PRODUCT_PRICE}}원</span>
          <span class="info-comment">0</span>
          <span class="info-view">{{posts.PAGEVIEW}}</span>
          <span class="info-like">0</span>
        </div>
        <div class="space"></div>
        <div class="article-description">
          <p v-html="posts.PRODUCT_DESCRIPTION">
          </p>
          <p class="date-text">{{posts.REGDATE}}</p>
        </div>
      </section>
      <section class="section">
        <div class="section-title">
          판매자 정보
        </div>
        <div class="space"></div>
        <div class="profile">
          <div class="profile-card">
            <div class="profile-thumb">
              <img :src="`http://211.53.209.242:8080/static/${posts.USER_THUMB}`" class="profile-thumb-image" />
            </div>
            <div class="profile-text">
              <p>{{posts.AUTHOR_NAME}}</p>
              <p>{{posts.PHONE}}</p>
            </div>
          </div>
          <div class="profile-data">
            <p class="data-name">판매 지역</p>
            <p class="data-contents">{{posts.PRODUCT_AREA}}</p>
          </div>
        </div>
      </section>
      <section class="section" v-if="this.otherPosts.length > 0">
        <div class="section-title">
          판매자의 다른 상품 ({{otherPosts.length}})
        </div>
        <div class="space"></div>
        <ion-grid class="item-list-grid" padding="9.7">
            <ion-row>
                <ion-col col-4 v-for="(data, index) in otherPosts" :key="index">
                  <div class="list-item-wrapper" @click="goView(data.SEQ)">
                    <img :src="'http://211.53.209.242:8080/static/' + getOtherImage(index)" class="list-item-thumb" />
                    <p class="list-item-price">{{data.PRODUCT_PRICE}}원</p>
                    <p class="list-item-title">{{data.PRODUCT_NAME}}</p>
                    <p class="list-item-author"></p>
                  </div>
                </ion-col>
            </ion-row>
        </ion-grid>
      </section>
    </div>
    <div class="bottom" v-if="isMyView">
      <div class="button-like"></div>
      <div class="button-edit" @click="doEdit()">수정하기</div>
    </div>
  </div>
</template>

<script>
//const imagePath = 'http://211.53.209.242:8080';
import Header from '@/components/Header'
import { Carousel, Slide } from 'vue-carousel';
import API from '@/api.js'

const textLineBreak = (text) => {
  return text.replace(/\n/g, "<br/>");
}

export default {
  name: 'ViewPage',
  components: {
    'Carousel': Carousel,
    'Slide': Slide,
    'top-menu': Header
  },
  data: function () {
    return {
      viewType: 'col-4',
      user: {},
      userInfo: {},
      posts: {
        PRODUCT_DESCRIPTION: "",
        PHOTO: []
      }, // 게시물
      commentLists: [], // 후기
      otherPosts: [], // 작성자 다른 게시물
      commentPost: { // 후기 폼
        active: false,
        marketSeq: null,
        userName: '',
        accessToken: '',
        content: ''
      },    
      slideOpts : {
        initialSlide: 1,
        speed: 400,
        slidesPerView: 3,
        coverflowEffect: {
          rotate: 50,
          stretch: 0,
          depth: 100,
          modifier: 1,
          slideShadows: true,
        },
      }
    }
  },
  created() {
    this.userInfo = this.$store.getters.getUser;
    if(!this.userInfo.accessToken){
       this.commentPost.active = false;
    } else{
      this.commentPost.active = true;
      this.commentPost.userName = this.userInfo.userName;
      this.commentPost.accessToken = this.userInfo.accessToken;
    } 
    this.getContents();
    this.getComments();
  },  
  computed: {
    isMyView (){
      return (this.posts.USERID == this.userInfo.userName) ? true : false;
    },
    isActive (){
      return (this.posts.ACTIVE == "Y") ? true : false;
    },
    isStatus (){
      return (this.posts.PRODUCT_STATUS == "new") ? true : false;
    },
    isDelivery (){
      return (this.posts.PRODUCT_DELIVERY == "direct") ? true : false;
    },
    isFee (){
      return (this.posts.PRODUCT_FEE == "extra") ? true : false;
    }    
  },
  methods: {
    getImageURI($filename) {
      return API.serverURI() + "/static/" + $filename;
    },
    goView($id) {
      this.$router.push({ name: 'View', params: { id: $id }})
    },
    doComment () {
      API.add(this.commentPost, "/comment")
        .then(res => {
          //console.log(res);
          if(res.data.success){
             alert('후기를 작성하였습니다.');
             this.getComments();
          }
          else alert('작성 도중 오류가 발생하였습니다.');
        }).catch((e) => {
          alert("서버와의 연결에 실패하였습니다." + e.message);
          //console.error(JSON.parse(e));
        });
    },
    doCount() {
      API.add({marketSeq : this.posts.SEQ}, "/count").catch((e) => {
          alert("서버와의 연결에 실패하였습니다." + e.message);
          //console.error(JSON.parse(e));
        });
    },
    doLike() {
      API.add(this.commentPost, "/like")
        .then(res => {
          if(!res.data.success) alert(res.data.msg);
        }).catch((e) => {
          alert("서버와의 연결에 실패하였습니다." + e.message);
          //console.error(JSON.parse(e));
        });
    },
    doEdit() {
      this.$router.push({ name: 'Edit', params: { id: this.posts.SEQ }})
    },
    getComments () {
      let seq = this.$route.params.id || null;
      if(!seq){
        return false;
      }
      API.find({}, "/comment/" + seq)
        .then(res => {
          this.commentLists = res.data.data;
        }).catch((e) => {
          alert("서버와의 연결에 실패하였습니다." + e.message);
          //console.error(JSON.parse(e));
        });
    },
    getContents () {
      let seq = this.$route.params.id || null;
      if(!seq){
        alert("잘못된 접근입니다.");
        return false;
      }
      API.find({}, "/article/" + seq)
        .then(res => {
          this.posts = res.data.data[0];
          this.posts.PRODUCT_DESCRIPTION = textLineBreak(this.posts.PRODUCT_DESCRIPTION);
          this.posts.PHOTO = JSON.parse(this.posts.PHOTO);
          this.commentPost.marketSeq = this.posts.SEQ;
          this.getOther(this.posts.AUTHOR);
          this.doCount();
        }).catch((e) => {
          alert("서버와의 연결에 실패하였습니다." + e.message);
          //console.error(JSON.parse(e));
        });
    },
    getOther (seq) {
      if(!seq){
        return false;
      }
      API.find({}, "/other/" + seq)
        .then(res => {
          this.otherPosts = res.data.data;
        }).catch((e) => {
          alert("서버와의 연결에 실패하였습니다." + e.message);
          //console.error(JSON.parse(e));
        });
    },
    getOtherImage(index){
      let photos = JSON.parse(this.otherPosts[index].PHOTO);
      return photos[0];
    },
    getCommentContents(text){
      return textLineBreak(text);
    },
    goCheat(){
      window.open('https://thecheat.co.kr/rb/?mod=_search', '_system');  
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/styles/global.scss";
@import "@/styles/pages/view.scss";
</style>
