import axios from 'axios'

//const serverURI = 'http://121.168.143.155:8080'
//const serverURI = 'http://192.168.42.211:8080';
const serverURI = 'http://211.53.209.242:8080'
//const serverURI = 'http://localhost:8080'

/*
const axiosConfig = {
  headers: {
      'Content-Type': 'application/x-www-form-urlencoded',
      "Access-Control-Allow-Origin": "*",
  }
};
*/
const enhanceAccessToeken = () => {
  const {accessToken} = localStorage
  if (!accessToken) return;
  axios.defaults.headers.common['Authorization'] = `Bearer ${accessToken}`;
  axios.defaults.headers.common['Content-Type'] = `application/x-www-form-urlencoded`;
}
enhanceAccessToeken()

export default {
  find : (parameter = {}, url = '/') => {
    return axios.get(`${serverURI}${url}`, {
      params: parameter,
      timeout: 1000
    })
  },
  add : (parameter, url = '/') => {
    return axios({
        method: 'post',
        url: `${serverURI}${url}`,
        headers: {
            'Content-Type': 'application/json'
        },
        data: parameter
    });
  },
  update : (target, parameter) => {
    return axios({
        method: 'put',
        url: `${serverURI}/${target}`,
        data: parameter,
        timeout: 12000
    });
  },
  delete : (target, url = '/') => {
    return axios.put(`${serverURI}${url}/${target}`, {
      timeout: 1000
    });
  },
  auth : (parameter, url = '/') => {
    return axios({
        method: 'post',
        url: `${serverURI}${url}`,
        headers: {
            'Content-Type': 'application/json'
        },
        data: parameter
    });
  },
  multipart : (formData, url = '/') => {
    return axios.post(`${serverURI}${url}`,
      formData,
      {
        headers: {
            'Content-Type': 'multipart/form-data'
        }
      }
    ).then(function(res){
      return res;
    });
  },
  instance : (contentType) => {
    if(!contentType) contentType = "multipart/form-data"
    return axios.create({
      baseURL: serverURI,
      timeout: 5000,
      headers: {'Content-Type': contentType}
    });
  },
  serverURI : () => {
    return serverURI;
  }
}
