<template>
  <section class="title">
    <div class="head">
      <div class="prev" @click="doPrev()" v-if="prevBtn"></div>
        <p class="title-text">{{titleName}}</p>
      <div class="setup" v-if="mode == 'SETUP'" @click="goMyEdit()"></div>
      <div class="edit" v-if="mode == 'EDIT'" @click="doSubmit()">수정</div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'Title',
  props: {
    titleName: {
        type: String,
        default: ''
    },
    prevBtn: {
        type: Boolean,
        default: true
    },
    mode: {
        type: String,
        default: 'SETUP'
    }
  },
  methods: {
    doPrev () {
      this.$router.push({ name: 'List'});
    },
    goMyEdit () {
      this.$router.push({ name: 'MyEdit'});
    },
    doSubmit() {
      this.$emit("frmSubmit");
    }
  }
}
</script>
<style lang="scss" scoped>
@import "@/styles/global.scss";
@import "@/styles/components/title.scss";
</style>
