<template>
  <div class="Write">
    <top-menu/>
    <div class="contents">
      <section class="article">
        <p class="article-text">카테고리</p>
        <category v-on:item="setCategory" :selected="category"/>
      </section>
      <section class="article">
        <p class="article-text">사진 등록하기</p>
        <div class="article-preview">
          <vue-upload-multiple-image
          @upload-success="uploadImageSuccess"
          @before-remove="beforeRemove"
          @edit-image="editImage"
          :data-images="uploadPhotos"
          dragText="드래그 앤 드롭"
          browseText="파일을 선택하세요"
          primaryText="사진을 맨 앞으로"
          popupText="첫 사진이 메인사진이 됩니다."
          dropText=""
          ></vue-upload-multiple-image>
        </div>
      </section>
      <section class="article">
        <p class="article-text">상품명</p>
        <input type="text" name="product-name" class="article-input" v-model="formData.productName" />
      </section>
      <section class="article">
        <p class="article-text">판매가격</p>
        <input type="text" name="product-price" class="article-input" v-model="formData.productPrice" />
      </section>
      <section class="article">
        <p class="article-text">판매유형</p>
        <div class="row">
          <div class="article-radio-button">
            <input type="radio" id="status-first" name="product-status" value="new" v-model="formData.productStatus" />
            <label for="status-first">새제품</label>
            <input type="radio" id="status-second" name="product-status" value="old" v-model="formData.productStatus" />
            <label for="status-second">중고</label>
          </div>
        </div>
        <div class="row">
          <div class="article-radio-button">
            <input type="radio" id="delivery-first" name="product-delivery" value="direct" v-model="formData.productDelivery" />
            <label for="delivery-first">직거래</label>
            <input type="radio" id="delivery-second" name="product-delivery" value="delivery" v-model="formData.productDelivery" />
            <label for="delivery-second">택배</label>
          </div>
          <div class="article-radio-button">
            <input type="radio" id="fee-first" name="product-fee" value="free" v-model="formData.productFee" />
            <label for="fee-first">무료 배송</label>
            <input type="radio" id="fee-second" name="product-fee" value="extra" v-model="formData.productFee" />
            <label for="fee-second">배송비 별도</label>
          </div>
        </div>
      </section>
      <section class="article">
        <p class="article-text">판매지역</p>
        <input type="text" name="product-area" class="article-input" v-model="formData.productArea" />
      </section>
      <section class="article">
        <p class="article-text">상품 상세 설명</p>
        <textarea  class="article-textarea" v-model="formData.productDescription" placeholder="* 판매수량은 필수 기재 사항입니다."></textarea>
      </section>
      <section class="article">
        <button class="regist-article" @click="submit()">등록 완료</button>
      </section>
    </div>
  </div>
</template>

<script>
import Header from '@/components/Header'
//import Title from '@/components/Title'
import Category from '@/components/Category'
//import draggable from "vuedraggable";
import VueUploadMultipleImage from 'vue-upload-multiple-image'
import API from '@/api.js'
import _ from 'lodash'

const formValidation = (frm) => {
  for (let [key, value] of Object.entries(frm)) {
      if(key == "category" && !value){
        alert("카테고리를 선택해주세요!");
        return false;
      }
      if(key == "author" && value == ""){
        alert("잘못된 시도입니다. 회원정보를 확인할 수 없습니다.");
        return false;
      }
      if(key == "accessToken" && value == ""){
        alert("잘못된 시도입니다. 회원정보를 확인할 수 없습니다.");
        return false;
      }
  }
  return frm;
}
export default {
  name: 'Edit',
  components: {
    'category': Category,
    'top-menu': Header,
    'VueUploadMultipleImage': VueUploadMultipleImage
  },
  data: function () {
    return {
      seq: null,
      userInfo: null,
      imagePicker: false,
      resultMedias: [],
      uploadPhotos: [],
      dragging: false,
      category: null,
      title: '',
      price: '',
      typeStatus : 'new',
      typeTrade : 'direct',
      typeDelivery : 'free',
      telStart : "1",
      telEnd : "24",
      position : "",
      description : "",
      formData : {
        token : "",
        author : "",
        accessToken : "",
        productName : "",
        productPrice : 0,
        productStatus : "new",
        productDelivery : "direct",
        productFee : "extra",
        productSaleStart : "",
        productSaleEnd : "",
        productArea : "",
        productDescription : "",
        photos : [],
        category : null
      }
    }
  },
  created() {
    this.formData.token = "GM" + Math.floor(Math.random() * 10000);
    this.userInfo = this.$store.getters.getUser;
    if(!this.userInfo.accessToken || this.userInfo.accessToken == ""){
      alert('로그인이 필요합니다.');
      this.$router.push({ name: 'Login'});
      return false;
    } else {
      this.getContents();
    }
  },
  computed: {
    isAddImage () {
      if(this.resultMedias > 9) return false;
      else return true;
    }
  },
  methods: {
    getContents () {
      let seq = this.$route.params.id || null;
      if(!seq){
        alert("잘못된 접근입니다.");
        return false;
      }
      API.find({}, "/article/" + seq)
        .then(res => {
          let posts = res.data.data[0];
          this.formData.token = posts.TOKEN;
          this.formData.author = posts.AUTHOR;
          this.formData.productName = posts.PRODUCT_NAME;
          this.formData.productArea = posts.PRODUCT_AREA;
          this.formData.productDelivery = posts.PRODUCT_DELIVERY;
          this.formData.productFee = posts.PRODUCT_FEE;
          this.formData.productPrice = posts.PRODUCT_PRICE;
          this.formData.productSaleStart = posts.PRODUCT_SALE_START;
          this.formData.productSaleEnd = posts.PRODUCT_SALE_END;
          this.formData.productStatus = posts.PRODUCT_STATUS;
          this.formData.category = posts.CATEGORY;
          this.category = posts.CATEGORY;
          this.seq = seq;
          this.getComments(seq);
        }).catch((e) => {
          alert("서버와의 연결에 실패하였습니다." + e.message);
          //console.error(JSON.parse(e));
        });
    },
    submit () {
      this.formData.category = this.category;
      this.formData.author = this.userInfo.userName;
      this.formData.accessToken = this.userInfo.accessToken;
      let $formData = formValidation(this.formData);
      if($formData){
        API.update(this.seq ,$formData, '/edit').then(res => {
          if(!res.data.success) alert(res.data.msg);
          else this.$router.push({ name: 'List'});
        }).catch((e) => {
          alert("서버와의 연결에 실패하였습니다." + e.message);
          //console.error(JSON.parse(e));
        })
      }
    },
    setSplice (i) {
      this.resultMedias = this.resultMedias.splice(i, 1);
    },
    setGallery () {
      this.imagePicker = !this.imagePicker;
    },
    setCategory (item) {
      this.category = item;
    },
    getPhoto () {
      var _this = this;
      let options = {
        quality: 75,
        allowEdit: true,
        targetWidth: 450,
        saveToPhotoAlbum: false,
        maximumImagesCount: 8,
        outputType: 1
      }
      window.imagePicker.getPictures(
        function(results) {
            for (var i = 0; i < results.length; i++) {
              _this.formData.photos.push(results[i]);
              _this.resultMedias.push({
                id: (_this.resultMedias.length - 1) + i,
                url: 'data:image/jpeg;base64,' + results[i]
              });
            }
            return _this.sortPhoto();
          }
      , () => {
        return false;
      }, options);
    },
    getComments (seq) {
      if(!seq){
        return false;
      }
      API.find({}, "/comment/" + seq)
        .then(res => {
          this.commentLists = res.data.data;
        }).catch((e) => {
          alert("서버와의 연결에 실패하였습니다." + e.message);
          //console.error(JSON.parse(e));
        });
    },
    doTransferFiles (){
      let results = this.formData.photos;
      for (var i = 0; i < results.length; i++) {
        var fileURL = results[i];
        var options = new FileUploadOptions(); // eslint-disable-line
        options.fileKey = "file";
        options.fileName = fileURL.substr(fileURL.lastIndexOf('/') + 1);
        options.mimeType = "image/jpg";
        var ft = new FileTransfer(); // eslint-disable-line
        ft.upload(fileURL, encodeURI("http://121.168.143.155:8080/upload"), (r) => {
          alert(r.response);
        }, (e) => {
          alert(JSON.stringify(e));
        }, options);
      }
    },
    sortPhoto () {
      let media = _.orderBy(this.resultMedias, ['id'],['asc']);
      this.resultMedias = media;
    },
    uploadImageSuccess(formData, index, fileList) {
      console.log('index', index, fileList)
      let $this = this;
      API.multipart(formData, '/upload').then(response => {
        $this.formData.photos.push(response.data.data[0].filename);
      })
    },
    beforeRemove (index, done, fileList) {
      console.log('index', index, fileList)
      //var r = confirm("해당 이미지를 삭제하시겠습니까?")
      //if (r == true) {
      //  
      //  done();
      //} else {
      //}
    },
    editImage (formData, index, fileList) {
      console.log('edit data', formData, index, fileList)
    }
  },
}
</script>

<style lang="scss" scoped>
@import "@/styles/global.scss";
@import "@/styles/pages/write.scss";
</style>
