import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    count: 0,
    accessToken: null,
    userName: null,
    userNo: null
  },
  mutations: {
    increment (state) {
      state.count++
    },
    LOGIN (state, {accessToken, userName}){
      state.accessToken = accessToken;
      state.userName = userName;
      localStorage.setItem('GOLFMART_TOKEN', accessToken);
      localStorage.setItem('GOLFMART_NAME', userName);
    },
    LOGOUT (state) {
      state.accessToken = null
      localStorage.removeItem('GOLFMART_TOKEN');
    }
  },
  getters: {
    getUser: state => {
      return {
        userName : state.userName,
        accessToken : state.accessToken
      }
    }
  },
  actions: {
    LOGIN ({commit}, {accessToken, userName}) {
      commit('LOGIN', {accessToken: accessToken, userName: userName});
    },
    LOGOUT ({commit}) {
      commit('LOGOUT');
    }
  }
})