<template>
  <div class="Write">
    <top-menu/>
    <div class="contents">
      <section class="article">
        <p class="article-text">카테고리</p>
        <category v-on:item="setCategory"/>
      </section>
      <section class="article">
        <p class="article-text">사진 등록하기</p>
        <div class="article-preview">
          <vue-upload-multiple-image
          @upload-success="uploadImageSuccess"
          @before-remove="beforeRemove"
          @edit-image="editImage"
          :data-images="uploadPhotos"
          dragText="드래그 앤 드롭"
          browseText="파일을 선택하세요"
          primaryText="사진을 맨 앞으로"
          popupText="첫 사진이 메인사진이 됩니다."
          dropText=""
          ></vue-upload-multiple-image>
        </div>
      </section>
      <section class="article">
        <p class="article-text">상품명</p>
        <input type="text" name="product-name" class="article-input" v-model="formData.productName" />
      </section>
      <section class="article">
        <p class="article-text">판매가격</p>
        <input type="text" name="product-price" class="article-input" v-model="formData.productPrice" />
      </section>
      <section class="article">
        <p class="article-text">판매유형</p>
        <div class="row">
          <div class="article-radio-button">
            <input type="radio" id="status-first" name="product-status" value="new" v-model="formData.productStatus" />
            <label for="status-first">새제품</label>
            <input type="radio" id="status-second" name="product-status" value="old" v-model="formData.productStatus" />
            <label for="status-second">중고</label>
          </div>
        </div>
        <div class="row">
          <div class="article-radio-button">
            <input type="radio" id="delivery-first" name="product-delivery" value="direct" v-model="formData.productDelivery" />
            <label for="delivery-first" class="third">직거래</label>
            <input type="radio" id="delivery-second" name="product-delivery" value="delivery" v-model="formData.productDelivery" />
            <label for="delivery-second" class="third">택배</label>
            <input type="radio" id="delivery-third" name="product-delivery" value="all" v-model="formData.productDelivery" />
            <label for="delivery-third" class="third">직거래 + 택배</label>
          </div>
          <div class="article-radio-button">
            <input type="radio" id="fee-first" name="product-fee" value="free" v-model="formData.productFee" />
            <label for="fee-first">무료 배송</label>
            <input type="radio" id="fee-second" name="product-fee" value="extra" v-model="formData.productFee" />
            <label for="fee-second">배송비 별도</label>
          </div>
        </div>
      </section>
      <section class="article">
        <p class="article-text">판매지역</p>
        <input type="text" name="product-area" class="article-input" v-model="formData.productArea" />
      </section>
      <section class="article">
        <p class="article-text">상품 상세 설명</p>
        <textarea  class="article-textarea" v-model="formData.productDescription" placeholder="* 판매수량은 필수 기재 사항입니다."></textarea>
      </section>
      <section class="article">
        <button class="regist-article" @click="submit()">등록 완료</button>
      </section>
    </div>
    <div class="image-selector" v-show="imagePicker">
      <write-title :title-name="'사진 등록하기'" :prevBtn="false"/>
      <div class="image-grid">
        <draggable
          :list="resultMedias"
          ghost-class="ghost"
          @start="dragging = true"
          @end="dragging = false"
        >
        <img class="image-thumb" v-for="(item, index) in resultMedias" :key="`images-${index}`" :src="item.url" @dblclick="setSplice(index)"/>
        </draggable>
      </div>
      <div class="image-bottom">
        <button class="regist-image" @click="getPhoto()">사진 등록</button>
        <button class="regist-close" @click="setGallery()">닫기</button>
      </div>
    </div>
  </div>
</template>

<script>
import Header from '@/components/Header'
import Title from '@/components/Title'
import Category from '@/components/Category'
import draggable from "vuedraggable";
import VueUploadMultipleImage from 'vue-upload-multiple-image'
import API from '@/api.js'
import _ from 'lodash'

const formValidation = (frm) => {
  for (let [key, value] of Object.entries(frm)) {
      if(key == "category" && !value){
        alert("카테고리를 선택해주세요!");
        return false;
      }
      if(key == "author" && value == ""){
        alert("잘못된 시도입니다. 회원정보를 확인할 수 없습니다.");
        return false;
      }
      if(key == "accessToken" && value == ""){
        alert("잘못된 시도입니다. 회원정보를 확인할 수 없습니다.");
        return false;
      }
  }
  return frm;
}

export default {
  name: 'Write',
  components: {
    'write-title': Title,
    'category': Category,
    'draggable': draggable,
    'top-menu': Header,
    'VueUploadMultipleImage': VueUploadMultipleImage,
  },
  data: function () {
    return {
      userInfo: null,
      user: {
        USERID: "",
        PHONE: ""
      },
      imagePicker: false,
      resultMedias: [],
      uploadPhotos: [],
      dragging: false,
      category: null,
      title: '',
      price: '',
      typeStatus : 'new',
      typeTrade : 'direct',
      typeDelivery : 'free',
      telStart : "1",
      telEnd : "24",
      position : "",
      description : "",
      transfer : null,
      formData : {
        token : "",
        author : "",
        accessToken : "",
        productName : "",
        productPrice : 0,
        productStatus : "new",
        productDelivery : "direct",
        productFee : "extra",
        productSaleStart : "",
        productSaleEnd : "",
        productArea : "",
        productDescription : "",
        photos : [],
        category : null
      }
    }
  },
  created() {
    this.formData.token = "GM" + Math.floor(Math.random() * 10000);
    this.userInfo = this.$store.getters.getUser;
    this.uploadPhotos = [];
    if(!this.userInfo.accessToken || this.userInfo.accessToken == ""){
      alert('로그인이 필요합니다.');
      this.$router.push({ name: 'Login'});
      return false;
    } else this.getMyInfo();
  },
  computed: {
    isAddImage () {
      if(this.resultMedias > 9) return false;
      else return true;
    }
  },
  methods: {
    submit () {
      this.formData.category = this.category;
      this.formData.author = this.userInfo.userName;
      this.formData.accessToken = this.userInfo.accessToken;
      let $formData = formValidation(this.formData);
      if(!$formData) return false;
      console.log("Form Validation : OK"); // eslint-disable-line
      if(this.uploadPhotos.length > 0){
        let $transfer = this.doTransferFiles(); // async
        if($transfer.success){
          for(var i = 0; i < $transfer.data.length; i++){
            let uri = $transfer.data[i].substring($transfer.data[i].lastIndexOf('/')+1);
            this.formData.photos.push(uri);
          }
        }
      }
      if($formData){
        console.log("Try Form Submit..."); // eslint-disable-line
        API.add($formData, '/add').then(res => {
          console.log("Form Submit : OK"); // eslint-disable-line
          this.posts = res.data.data;
          if(!res.data.success) alert(res.data.msg);
          else this.$router.push({ name: 'List'});
        }).catch((e) => {
          alert("서버와의 연결에 실패하였습니다." + e.message);
          console.log("Form Submit : FAIL"); // eslint-disable-line
        })
      }
    },
    getMyInfo () {
      if(this.userInfo.accessToken || this.userInfo.accessToken != ""){
      API.auth({token : this.userInfo.accessToken}, "/user")
        .then(res => {
          if(!res.data.data) throw new Error('정보를 찾을 수 없습니다.');
          this.user = res.data.data[0];
        }).catch((e) => {
          alert("서버와의 연결에 실패하였습니다." + e.message);
          //console.error(JSON.parse(e));
        });
      }
    },
    setSplice (i) {
      this.resultMedias = this.resultMedias.splice(i, 1);
    },
    setGallery () {
      this.imagePicker = !this.imagePicker;
    },
    setCategory (item) {
      this.category = item;
    },
    sendFiles() {

    },
    doTransferFiles (){
      let files = this.uploadPhotos;
      let failure = 0;

      var uri = encodeURI(API.serverURI() + "/upload");
      let ft = new FileTransfer();  // eslint-disable-line   
      if(files.length > 0){
        for (let [key, f] of Object.entries(files)){ // eslint-disable-line
          let transferOption = new FileUploadOptions(); // eslint-disable-line   
          transferOption.fileKey = "file";
          transferOption.fileName = f.substr(f.lastIndexOf('/') + 1);
          transferOption.mimeType = "image/jpeg"
          ft.upload(f, uri, null, fail, transferOption);
          console.log("File Transfer : OK"); // eslint-disable-line
        }
        var fail = function (error) {
          failure++;
          console.log("File Transfer : FAIL (" + error.source + ")"); // eslint-disable-line
        }
        return (failure > 0) ? { success: false, data: [] } : { success: true, data: files };
      } else return { success: false, data: [] };
    },
    sortPhoto () {
      let media = _.orderBy(this.resultMedias, ['id'],['asc']);
      this.resultMedias = media;
    },
    uploadImageSuccess(formData, index, fileList) {
      console.log(index,fileList);
      let $this = this;
      API.multipart(formData, '/upload').then(response => {
        $this.formData.photos.push(response.data.data[0].filename);
      })
    },
    beforeRemove (index, done, fileList) {
      console.log('index', index, fileList)
      //var r = confirm("해당 이미지를 삭제하시겠습니까?")
      //if (r == true) {
      //  
      //  done();
      //} else {
      //}
    },
    editImage (formData, index, fileList) {
      console.log('edit data', formData, index, fileList)
    }
  },
}
</script>

<style lang="scss" scoped>
@import "@/styles/global.scss";
@import "@/styles/pages/write.scss";
</style>
