<template>
    <div class="container">
        <router-view></router-view>
    </div>
</template>

<script>

export default {
  name: 'App',
  created(){
    this.getAuth();
  },
  methods: {
    getAuth(){
      let $token = localStorage.getItem('GOLFMART_TOKEN');
      let $name = localStorage.getItem('GOLFMART_NAME');
      if($token == undefined || $token == "" || $name == undefined || $name == "") return false;
      this.$store.dispatch('LOGIN', {accessToken : $token, userName : $name});
    }
  },
}
</script>
<style lang="scss">
@import "@/styles/global.scss";
body {
  background-color: $color_bg;
  .container {
    width: 100%;
    height: 100%;
      overflow-x: hidden;
      overflow-y: auto;
  }
}
</style>
