<template>
  <div class="Login">
    <div class="contents">
      <div class="logo"></div>
      <div class="login-form">
        <p class="login-subtitle">판매 관리페이지</p>
        <!--div class="login-field id">
          <input type="text" name="id" v-model="username" class="input-field" placeholder="휴대폰 번호 입력" />
        </div>
        <div class="login-field pw">
          <input type="password" name="pw" v-model="password" class="input-field"  placeholder="비밀번호 입력" />
        </div>
        <button type="button" class="login-submit" @click="goLogin()">로그인</button-->
        <!--button type="button"  @click="goKakao()">카카오톡 로그인</button-->
        <div class="login-kakao">
          <KakaoLogin
            api-key="efda991ab184221e23ed706a08ab4488"
            image="kakao_account_login_btn_medium_wide"
            :on-success="onSuccess"
            :on-failure="onFailure"
            error-message="현재 카카오 로그인 서비스를 이용할 수 없습니다. 잠시후 다시 시도해주세요."
            />
          </div>
      </div>
      <!--div class="bottom">
        <button type="button" class="button-findpw">비밀번호 찾기</button>
        <button type="button" class="button-join" @click="goJoin()">회원가입</button>
      </div-->
    </div>
  </div>
</template>

<script>
import KakaoLogin from 'vue-kakao-login'
import API from '@/api.js'

export default {
  name: 'Login',
  components: {
    'KakaoLogin' : KakaoLogin
  },
  data() {
    return {
      token : 'TEST_ACCESS',
      username : '',
      password : ''
    }
  },
  methods: {
    goLogin () {
      API.auth({username: this.username, password: this.password}, '/login')
        .then(res => {
          if(!res.data.success) throw new Error(res.data.msg);
          if(res.data.token != undefined){
            this.$store.dispatch('LOGIN', {accessToken : res.data.token, userName : res.data.username})
            .then(() => {
              alert("골프마트에 오신 것을 환영합니다.\n( 로그인 성공:" + new Date() +" )");
              this.$router.push({name: 'List'});
            });
          }
        }).catch((e) => {
          alert("서버와의 연결에 실패하였습니다." + e.message);
        });
    },
    onSuccess (res) {
      let $this = this;
      API.auth({ 'token' : res.access_token }, '/sns').then(user => {
        $this.$store.dispatch('LOGIN', {accessToken : res.access_token, userName : user.data.username})
        .then(() => {
          alert("골프마트에 오신 것을 환영합니다.\n( 로그인 성공:" + new Date() +" )");
          $this.$router.push({name: 'List'});
        });
      })
    },
    onFailure (res) {
      console.warn(res.error_description);
    },
    goJoin() {
      this.$router.push({name: "Join"});
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/styles/global.scss";
@import "@/styles/pages/login.scss";
</style>
